<template>
  <v-card class="invoice-owed">
    <v-card-title style="padding-bottom:0px">
      {{ title }}
      <v-spacer/>
      <v-btn v-if="showNewItemBtn" color="green" dark @click.stop="$emit('new-item-clicked')">{{ newItemLabel }}</v-btn>

      <v-menu :close-on-content-click="true" bottom offset-y>
        <template v-slot:activator="{ on }">
          <v-btn v-if="menu.length > 0" color="green" text v-on="on" style="margin-right:-16px">
            <v-icon>mdi-menu</v-icon>
          </v-btn>
        </template>

        <template>
          <v-list style="min-width:120px">
            <template v-for="(item, index) in menu">
              <template v-if="item.title !== 'divider'">
                <v-list-item @click="$emit('menu-clicked', item)" :key="index" no-action dense>
                  <!-- <v-list-item-action>
                    <v-icon color="white">{{ item.action}}</v-icon>
                  </v-list-item-action> -->
                  <v-list-item-content>
                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </template>
              <template v-else>
                <v-divider :key="index"></v-divider>
              </template>
            </template>
          </v-list>
        </template>
      </v-menu>
    </v-card-title>
    <v-card-text class="chart-parent" :style="parentStyle" >
      <VueApexCharts    
        type="bar"
        :width="width"
        :height="height"
        :options="options"
        :series="series"
      />
    </v-card-text>
  </v-card>
</template>

<style scoped>
.invoice-owed{
  /* max-width: 500px; */
  margin: 0px 10px 10px 0px;
}
.chart-parent{
  padding:0px 12px 0px 12px;
}
</style>

<script>
// @ is an alias to /src

export default {
  name: 'InvoiceOwed',
  components: {
    VueApexCharts: () => import('vue-apexcharts')
  },
  props: {
    title: {
      type: String,
      default: 'New Chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '220px'
    },
    newItemLabel: {
      type: String,
      default: 'New Item'
    },
    showNewItemBtn: {
      type: Boolean,
      default: false
    },
    menu:{
      type:Array,
      default: () => {
        return []
      }
    },
    series: Array,
    stacked: {
      type:Boolean,
      default:true
    },
    plotOptions: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data: () => ({    
    series1: [{
      name: 'series-1',
      data: [30, 40, 45, 50, 49, 60, 70, 91, 74, 80, 105, 125]
    }],
    defaultPlot:{
      bar: {
        startingShape: 'flat',
        endingShape: 'flat',
      }
    },
  }),
  computed:{
    options: {
      get() {
        var merge = require('lodash.merge')
        var _plotOptions = merge({}, this.defaultPlot, this.plotOptions)
        var x = {
          chart: {
            id: 'example',
            stacked: this.stacked,
            sparkline: {
              eenabled: true
            },
            toolbar: {
              tools: {
                download: false
              }
            }
          },
          plotOptions: _plotOptions,
          xaxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
          }
      }
      return x
    },
    parentStyle(){
      return `height:${this.height};padding:0px`
    }
  }
  }
}
</script>
